import {
  parseCreationOptionsFromJSON,
  supported,
  create,
} from '@github/webauthn-json/browser-ponyfill';
import { SERVER_URL } from '../App';
import { Button, Stack, TextField, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';

const registration = async (registerEmail: string, registerDisplayName: string) => {
  const request = await fetch(SERVER_URL + '/auth/request-register', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      email: registerEmail,
      displayName: registerDisplayName,
    }),
  });

  if (!request.ok) {
    return;
  }

  const authOptions = await request.json();


  try {
    const result = await create(parseCreationOptionsFromJSON(authOptions));
    const response = result.toJSON();

    await fetch(SERVER_URL + '/auth/register', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        id: response.rawId,
        data: response.response.clientDataJSON,
        type: response.type,
      }),
    });
  } catch (e) {
    console.error(e);
    await fetch(SERVER_URL + '/auth/abort-register', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        challenge: authOptions.publicKey.challenge,
      }),
    });
  }
};

function RegistrationForm() {
  const { register, handleSubmit } = useForm();

  const [state, setState] = useState({
    supported: false,
    registerDisplayName: '',
    registerEmail: '',
    loginEmail: '',
  });

  useEffect(() => {
    setState({
      ...state,
      supported: supported(),
    });
  }, []);

  const onSubmit = async (data: any) => {
    await registration(data.email, data.displayName);
  };

  if (!state.supported) {
    return <div>Not Supported</div>;
  }

  return <Stack
    spacing={1}
    component="form"
    onSubmit={handleSubmit(onSubmit)}
    noValidate
    autoComplete="off"
  >
    <Typography textAlign={'center'} variant={'h5'}>Registration</Typography>
    <TextField {...register('email')}
               id={'email'}
               key={'email'}
               name={'email'}
               placeholder={'Email'}
               label={'Email'}
               size="small"
    />
    <TextField {...register('displayName')}
               id={'displayName'}
               key={'displayName'}
               name={'displayName'}
               placeholder={'Display Name'}
               label={'Display Name'}
               size="small"
    />
    <Button size="small" variant="contained" type="submit">Login</Button>
  </Stack>;
}

export default RegistrationForm;
