import { get, parseRequestOptionsFromJSON } from '@github/webauthn-json/browser-ponyfill';
import { Button, Stack, TextField } from '@mui/material';
import React from 'react';
import { useForm } from 'react-hook-form';
import { SERVER_URL } from '../App';

const login = async (email?: string) => {
  let extraData = {};

  if (email) {
    extraData = {
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email,
      }),
    };
  }

  const request = await fetch(SERVER_URL + '/auth/request-login', {
    ...extraData,
    method: 'POST',
  });

  if (!request.ok) {
    return;
  }

  const authOptions = await request.json();
  const response = (
    await get(parseRequestOptionsFromJSON(authOptions))
  ).toJSON();

  const res = await fetch(SERVER_URL + '/auth/login', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      id: response.rawId,
      user: response.response.userHandle,
      data: response.response.clientDataJSON,
    }),
  });
  console.log(res);
};

function LoginForm() {
  const { register, handleSubmit } = useForm();
  const onSubmit = async (data: any) => {
    await login(data.email);
  };

  return <Stack
    spacing={1}
    component="form"
    onSubmit={handleSubmit(onSubmit)}
    noValidate
    autoComplete="off"
  >
    <TextField {...register('email')}
               id={'email'}
               key={'email'}
               name={'email'}
               placeholder={'Email'}
               label={'Email'}
               size="small"
    />
    <Button size="small" variant="contained" type="submit">Login</Button>
  </Stack>;
}

export default LoginForm;
